.ListTitle {
  background: var(--primary, linear-gradient(134deg, #bc3cd8 0%, #c54b8c 100%));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: Work Sans;
  font-size: 40px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  display: flex;
  align-items: flex-start;
}

.AboutMeContainer {
  width: 100%;
  display: flex;
  justify-content: space-between;

  @media (max-width: 768px) {
    flex-direction: column;
  }
}

.AboutMeText {
  padding: 1.875rem 0 0 0;
  color: #000;
  width: 363px;
  font-family: Work Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 110%;
  letter-spacing: -0.36px;

  @media (max-width: 768px) {
    width: 100%;
  }

  @media (max-width: 320px) {
    width: 100%;
  }
}

.Avatar {
  width: 345px;
  height: 328px;
  border-radius: 50%;

  @media (max-width: 768px) {
    margin-top: 2rem;
    width: 245px;
    height: 228px;
  }

  @media (max-width: 320px) {
    width: 145px;
    height: 138px;
  }
}
